import { Mat4, Quat, Vec3 } from '../math';
import enums from './enums';
import { AABB } from './aabb';

const _v3_tmp = new Vec3();
const _offset = new Vec3();
const _min = new Vec3();
const _max = new Vec3();
function maxComponent (v: Vec3) { return Math.max(Math.max(v.x, v.y), v.z); }

/**
 * @en
 * Basic Geometry: Sphere.
 * @zh
 *  
 */

export class Sphere {
    /**
     * @en
     * create a new sphere
     * @zh
     *  sphere 
     * @param cx @en X-Coordinate of center point relative to the origin.  @zh  X 
     * @param cy @en Y-Coordinate of center point relative to the origin.  @zh  Y 
     * @param cz @en Z-Coordinate of center point relative to the origin.  @zh  Z 
     * @param r @en Radius of the sphere. @zh 
     * @return @en return a new sphere. @zh  sphere
     */
    public static create (cx: number, cy: number, cz: number, r: number): Sphere {
        return new Sphere(cx, cy, cz, r);
    }

    /**
     * @en
     * clone a new sphere
     * @zh
     *  sphere 
     * @param p @en The sphere object to clone from. @zh 
     * @return @en The sphere object to clone to. @zh 
     */
    public static clone (p: Sphere): Sphere {
        return new Sphere(p.center.x, p.center.y, p.center.z, p.radius);
    }

    /**
     * @en
     * copy the values from one sphere to another
     * @zh
     *  sphere  sphere
     * @param out @en The sphere object to copy to. @zh  sphere
     * @param a @en The sphere object to copy from. @zh  sphere
     * @return @en The sphere object to copy to. @zh  sphere
     */
    public static copy (out: Sphere, p: Sphere): Sphere {
        Vec3.copy(out.center, p.center);
        out.radius = p.radius;

        return out;
    }

    /**
     * @en
     * create a new bounding sphere from two corner points
     * @zh
     *  sphere
     * @param out - @en Sphere created from points. @zh  sphere
     * @param minPos - @en Lower point of the sphere. @zh sphere 
     * @param maxPos - @en Upper point of the sphere. @zh sphere 
     * @returns @en The output sphere object to save the created sphere data. @zh  sphere
     */
    public static fromPoints (out: Sphere, minPos: Vec3, maxPos: Vec3): Sphere {
        Vec3.multiplyScalar(out.center, Vec3.add(_v3_tmp, minPos, maxPos), 0.5);
        out.radius = Vec3.subtract(_v3_tmp, maxPos, minPos).length() * 0.5;
        return out;
    }

    /**
     * @en
     * Set the components of a sphere to the given values
     * @zh
     * 
     * @param out @en The sphere to set properties to. @zh  sphere
     * @param cx @en X-Coordinate of center point relative to the origin.  @zh  X 
     * @param cy @en Y-Coordinate of center point relative to the origin.  @zh  Y 
     * @param cz @en Z-Coordinate of center point relative to the origin.  @zh  Z 
     * @param r @en Radius of the sphere. @zh 
     * @return @en Sphere which the properties will be set to. @zh  sphere
     * @function
     */
    public static set (out: Sphere, cx: number, cy: number, cz: number, r: number): Sphere {
        out.center.x = cx;
        out.center.y = cy;
        out.center.z = cz;
        out.radius = r;

        return out;
    }

    /**
     * @en
     * The center of this sphere.
     * @zh
     * 
     */
    protected _center: Vec3 = new Vec3(0, 0, 0);
    get center (): Vec3 {
        return this._center;
    }

    set center (val: Vec3) {
        this._center = val;
    }

    private _radius = 0;

    /**
      * @en
      * The radius of this sphere.
      * @zh
      * 
      */
    get radius (): number {
        return this._radius;
    }

    set radius (val: number) {
        this._radius = val;
    }

    /**
     * @en
     * Gets the type of the shape.
     * @zh
     * 
     */
    get type () {
        return this._type;
    }

    protected readonly _type: number;

    /**
     * @en
     * Construct a sphere.
     * @zh
     * 
     * @param cx @en The X-Coordinate of the sphere. @zh  X 
     * @param cy @en The Y-Coordinate of the sphere. @zh  Y 
     * @param cz @en The Z-Coordinate of the sphere. @zh  Z 
     * @param r @en The radius. @zh 
     */
    constructor (cx = 0, cy = 0, cz = 0, r = 1) {
        this._type = enums.SHAPE_SPHERE;
        this._center = new Vec3(cx, cy, cz);
        this._radius = r;
    }

    public destroy () {
    }

    /**
     * @en
     * Get a clone.
     * @zh
     * 
     */
    public clone () {
        return Sphere.clone(this);
    }

    /**
     * @en
     * Copy a sphere.
     * @zh
     * 
     * @param a @en The sphere to copy from. @zh 
     */
    public copy (a: Sphere) {
        return Sphere.copy(this, a);
    }

    /**
     * @en
     * Get the bounding points of this shape
     * @zh
     * 
     * @param minPos @en The point with maximum coordinates of the sphere. @zh 
     * @param maxPos @en The point with minimum coordinates of the sphere. @zh 
     */
    public getBoundary (minPos: Vec3, maxPos: Vec3) {
        Vec3.set(minPos, this.center.x - this.radius, this.center.y - this.radius, this.center.z - this.radius);
        Vec3.set(maxPos, this.center.x + this.radius, this.center.y + this.radius, this.center.z + this.radius);
    }

    /**
     * @en
     * Transform this shape
     * @zh
     *  out  sphere 
     * @param m @en The transform matrix. @zh 
     * @param pos @en The position. @zh 
     * @param rot @en The rotation. @zh 
     * @param scale @en The scale. @zh 
     * @param out @en The sphere which the transform will be applied to. @zh 
     */
    public transform (m: Mat4, pos: Vec3, rot: Quat, scale: Vec3, out: Sphere) {
        Vec3.transformMat4(out.center, this.center, m);
        out.radius = this.radius * maxComponent(scale);
    }

    /**
     * @en
     * Translate and rotate this sphere.
     * @zh
     *  out  sphere 
     * @param m @en The transform matrix. @zh 
     * @param rot @en The rotation. @zh 
     * @param out @en The sphere which the transform will be applied to. @zh 
     */
    public translateAndRotate (m: Mat4, rot: Quat, out: Sphere) {
        Vec3.transformMat4(out.center, this.center, m);
    }

    /**
     * @en
     * Scaling this sphere.
     * @zh
     *  out  sphere 
     * @param scale @en The scale. @zh 
     * @param out @en The sphere which the scale will be applied to. @zh 
     */
    public setScale (scale: Vec3, out: Sphere) {
        out.radius = this.radius * maxComponent(scale);
    }

    /**
     * @en The point to be merged.
     * @zh 
     * @param point @en The point to be merged. @zh 
     */
    public mergePoint (point: Vec3) {
        // if sphere.radius Less than 0,
        // Set this point as anchor,
        // And set radius to 0.
        if (this.radius < 0.0) {
            this.center.set(point);
            this.radius = 0.0;
        }

        Vec3.subtract(_offset, point, this.center);
        const dist = _offset.length();

        if (dist > this.radius) {
            const half = (dist - this.radius) * 0.5;
            this.radius += half;
            Vec3.multiplyScalar(_offset, _offset, half / dist);
            Vec3.add(this.center, this.center, _offset);
        }
    }

    /**
     * @en The sphere and points to be merged.
     * @zh 
     * @param points @en The point to be merged. @zh 
     */
    public mergePoints (points: Vec3[]) {
        const length = points.length;
        if (length < 1) return;

        // Init Invalid Sphere
        this.radius = -1.0;

        for (let i = 0; i < length; i++) {
            this.mergePoint(points[i]);
        }
    }

    /**
     * @en The axis-aligned bounding box to be merged.
     * @zh 
     * @param a @en Cube. @zh 
     */
    public mergeAABB (a: AABB) {
        a.getBoundary(_min, _max);

        this.mergePoint(_min);
        this.mergePoint(_max);
    }
}
