import { Vec3 } from '../math';
import enums from './enums';
import { IVec3Like } from '../math/type-define';

/**
 * @en
 * Basic Geometry: ray.
 * @zh
 *  
 */

export class Ray {
    /**
     * @en
     * create a new ray
     * @zh
     * 
     * @param {number} ox @en x component of start point @zh  x 
     * @param {number} oy @en y component of start point @zh  y 
     * @param {number} oz @en z component of start point @zh  z 
     * @param {number} dx @en Point in the x direction @zh  x 
     * @param {number} dy @en Point in the y direction @zh  y 
     * @param {number} dz @en Point in the z direction @zh  z 
     * @return {Ray} @en Ray object @zh 
     */
    public static create (ox = 0, oy = 0, oz = 0, dx = 0, dy = 0, dz = 1): Ray {
        return new Ray(ox, oy, oz, dx, dy, dz);
    }

    /**
     * @en
     * Creates a new ray initialized with values from an existing ray
     * @zh
     * 
     * @param {Ray} a @en The Ray object to be cloned from @zh 
     * @return {Ray} @en Clone new ray object @zh 
     */
    public static clone (a: Ray): Ray {
        return new Ray(
            a.o.x, a.o.y, a.o.z,
            a.d.x, a.d.y, a.d.z,
        );
    }

    /**
     * @en
     * Copy the values from one ray to another
     * @zh
     *  ray  ray
     * @param {Ray} out @en The ray object to be modified @zh  ray
     * @param {Ray} a @en The copied ray object @zh  ray
     * @return {Ray} @en Ray object @zh out  ray
     */
    public static copy (out: Ray, a: Ray): Ray {
        Vec3.copy(out.o, a.o);
        Vec3.copy(out.d, a.d);

        return out;
    }

    /**
     * @en
     * create a ray from two points
     * @zh
     * 
     * @param {Ray} out @en The ray object to be modified @zh 
     * @param {Vec3} origin @en Starting point of the ray @zh 
     * @param {Vec3} target @en point on the ray @zh 
     * @return {Ray} @en Ray object @zh out 
     */
    public static fromPoints (out: Ray, origin: Vec3, target: Vec3): Ray {
        Vec3.copy(out.o, origin);
        Vec3.normalize(out.d, Vec3.subtract(out.d, target, origin));
        return out;
    }

    /**
     * @en
     * Set the components of a ray to the given values
     * @zh
     * 
     * @param {Ray} out @en The ray object to be modified @zh 
     * @param {number} ox @en x component of start point @zh  x 
     * @param {number} oy @en y component of start point @zh  y 
     * @param {number} oz @en z component of start point @zh  z 
     * @param {number} dx @en Point in the x direction @zh  x 
     * @param {number} dy @en Point in the y direction @zh  y 
     * @param {number} dz @en Point in the z direction @zh  z 
     * @return {Ray} @en Ray object @zh out 
     */
    public static set (out: Ray, ox: number, oy: number, oz: number, dx: number, dy: number, dz: number): Ray {
        out.o.x = ox;
        out.o.y = oy;
        out.o.z = oz;
        out.d.x = dx;
        out.d.y = dy;
        out.d.z = dz;

        return out;
    }

    /**
     * @en
     * The origin of the ray.
     * @zh
     * 
     */
    public o: Vec3;

    /**
     * @en
     * The direction of the ray.
     * @zh
     * 
     */
    public d: Vec3;

    /**
     * @en
     * Gets the type of the shape.
     * @zh
     * 
     */
    get type () {
        return this._type;
    }

    protected readonly _type: number;

    /**
     * @en
     * Construct a ray;
     * @zh
     * 
     * @param {number} ox @en x component of start point @zh  x 
     * @param {number} oy @en y component of start point @zh  y 
     * @param {number} oz @en z component of start point @zh  z 
     * @param {number} dx @en Point in the x direction @zh  x 
     * @param {number} dy @en Point in the y direction @zh  y 
     * @param {number} dz @en Point in the z direction @zh  z 
     */
    constructor (ox = 0, oy = 0, oz = 0,
        dx = 0, dy = 0, dz = -1) {
        this._type = enums.SHAPE_RAY;
        this.o = new Vec3(ox, oy, oz);
        this.d = new Vec3(dx, dy, dz);
    }

    /**
     * @en
     * Compute a point with the distance between the origin.
     * @zh
     * 
     * @param out @en Another point on the ray @zh 
     * @param distance @en Given distance @zh 
     */
    public computeHit (out: IVec3Like, distance: number) {
        Vec3.normalize(out, this.d);
        Vec3.scaleAndAdd(out, this.o, out, distance);
    }
}
